import { SmallCaps } from "../components";

import React from "react";

export { LsosBasicButton };

function LsosBasicButton({ style }) {
  const emailSubject = encode("Lsos Basic");
  const emailBody = encode(body("I'm interested in using the Lsos Basic."));

  return (
    <a
      className="lsos-button contact-address"
      style={style}
      data-recipient="basic"
      data-subject={emailSubject}
      data-body={emailBody}
      data-alreadyencoded={true}
      data-jslink={true}
    >
      I'm interested in using the{" "}
      <SmallCaps className="lsos-button--action-bottom">Lsos Basic</SmallCaps>
    </a>
  );
}

function body(content) {
  const emailBegin = "Hi,\n\n";
  const emailEnd = "\n\nKind regards.\n\n";
  return emailBegin + content + emailEnd;
}

function encode(str) {
  return encodeURIComponent(str);
}
